.Nav{position:fixed;top:0;left:0;width:100%;padding:20px 20px;z-index:999}.Nav #Nav__menu{background-color:rgba(255,255,255,.95);position:fixed;left:0;right:0;top:0;bottom:0;display:flex;justify-content:center;align-items:center;text-align:center;transition:all .5s}.Nav #Nav__menu .Nav__menu__main h1{font-size:4em;margin:25px}.Nav #Nav__menu .Nav__menu__footer{position:fixed;bottom:0;left:0;right:0;justify-content:space-around;display:flex;font-family:"Space Grotesk",sans-serif;margin:10px;font-size:1.5em}.Nav .Nav__menu__open{height:100vh}.Nav .Nav__menu__close{overflow:hidden;height:0;opacity:0%}@media only screen and (min-width: 1000px){.Nav{padding:20px 150px}}#nav-icon4{width:calc(1.414213 * calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300))));height:calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300)));justify-self:right;transform:rotate(0deg);transition:.5s ease-in-out}#nav-icon4 span{display:block;position:absolute;height:2px;width:100%;background:#2a2a2a;border-radius:3px;opacity:1;left:0;transform:rotate(0deg);transition:.25s ease-in-out}#nav-icon4 span:nth-child(1){top:0px;transform-origin:left center}#nav-icon4 span:nth-child(2){top:calc(calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300))) / 2);transform-origin:left center}#nav-icon4 span:nth-child(3){top:calc(calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300))) / 1);transform-origin:left center}#nav-icon4.open span:nth-child(1){transform:rotate(45deg);top:0px;left:0px}#nav-icon4.open span:nth-child(2){width:0%;opacity:0}#nav-icon4.open span:nth-child(3){transform:rotate(-45deg);top:calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300)));right:0px}
.cursor{width:40px;height:40px;border:2px solid #ccc;border-radius:100%;position:fixed;transform:translate(-50%, -50%);pointer-events:none;transition:all 150ms ease;transition-property:background-color,opacity,transform,mix-blend-mode;z-index:9999;mix-blend-mode:difference}.cursor--hidden{opacity:0}.cursor--link-hovered{transform:translate(-50%, -50%) scale(1.25);background-color:#ccc}.cursor--clicked{transform:translate(-50%, -50%) scale(0.9);background-color:#ccc}html *,body *{cursor:none}
.Footer{background-color:#ccc}.Footer .contact__description{padding:0;display:flex;flex-direction:column;align-items:flex-end}.Footer .contact__description p{text-align:right;font-size:calc(18px + (30 - 18) * ((100vw - 300px) / (1600 - 300)));font-weight:600;max-width:calc(300px + (500 - 300) * ((100vw - 300px) / (1600 - 300)));padding:25px}.Footer .contact__description a{color:#fff}.Footer .contact__description a:hover{color:#2a2a2a}.Footer .copyright{padding:15px;display:flex;justify-content:center}@media only screen and (min-width: 1000px){.Footer .contact__description{padding:50px 150px}}
.About{margin-top:calc(50px + (100 - 40) * ((100vw - 300px) / (1600 - 300)));display:flex;justify-content:center;flex-direction:column}.About .About__title{display:flex;flex-direction:column;align-items:center;justify-content:center}.About .About__subtitle{margin-top:50px;color:rgba(0,0,0,.5);text-align:center;font-size:2.5em}.About__description{justify-self:flex-end;margin:calc(100px + (50 - 100) * ((100vw - 300px) / (1600 - 300))) 0;padding:0 3ch}.About__description p{font-style:italic;padding-bottom:25px}.About__description a{font-weight:700;text-decoration:underline}
.Work{padding:50px 20px}.Work h1{text-align:center}.Work h2{text-align:center}.Work .Work__category{display:flex;justify-content:space-around;align-items:center;margin:50px 0}.Work .Work__category svg{color:#ccc;height:calc(30px + (50 - 30) * ((100vw - 300px) / (1600 - 300)));width:calc(30px + (50 - 30) * ((100vw - 300px) / (1600 - 300)));transition:.5s color}.Work .Work__category .active{color:#2a2a2a}@media only screen and (min-width: 1000px){.Work{padding:50px 150px}}
.Work__grid{display:grid;grid-gap:10px;gap:10px;grid-template-columns:1fr 1fr}.Work__grid .Work__item{width:100%;background-color:#ddd;height:150px}@media only screen and (min-width: 1000px){.Work__grid{grid-template-columns:1fr 1fr 1fr}}
.resume{padding:0 20px;margin-top:75px}.resume h1{text-align:center;font-family:"Space Grotesk",sans-serif;font-size:calc(40px + (100 - 40) * ((100vw - 300px) / (1600 - 300)))}.resume h2{text-align:left;font-size:calc(18px + (30 - 18) * ((100vw - 300px) / (1600 - 300)));margin-bottom:15px}.resume .resume__section__grid{display:grid;grid-template-columns:1fr}.resume .resume__item{padding:0 10px}.resume .resume__item .resume__item__header{position:relative}.resume .resume__item .resume__item__header .resume__item__description__toggle svg{position:absolute;top:0;right:0;height:calc(30px + (50 - 30) * ((100vw - 300px) / (1600 - 300)));width:calc(30px + (50 - 30) * ((100vw - 300px) / (1600 - 300)))}.resume .resume__item .resume__item__inner{margin:0 0 0 25px}.resume .resume__item .resume__item__description{transition:.2s max-height;overflow:hidden;padding:0 25px}.resume .resume__item .resume__item__description li{list-style:circle}.resume .resume__item .resume__item__description__show{max-height:500px}.resume .resume__item .resume__item__description__hidden{max-height:0}@media only screen and (min-width: 1000px){.resume{padding:0 150px}.resume .resume__section__grid{grid-template-columns:1fr 1fr}}
.work__page{padding:0 20px;margin:75px 0}.work__page h1{font-family:"Space Grotesk",sans-serif;font-size:calc(40px + (100 - 40) * ((100vw - 300px) / (1600 - 300)))}.work__page .work__page__content .temp_images{width:100%}.work__page .work__page__content .work__text p{margin-top:10px}.work__page .back__button{margin-top:50px}@media only screen and (min-width: 1000px){.work__page{padding:0 150px}}
.blog{padding:0 20px;margin-top:75px;min-height:100vh}.blog h1{text-align:center}.blog .blog__grid{margin:25px 0}.blog .blog__grid .blog__card{display:grid;grid-template-columns:2fr 1fr;border:solid #000 1px;margin:25px 0}.blog .blog__grid .blog__card .blog__card__text{padding:15px}.blog .blog__grid .blog__card .blog__card__thumbnail{background-color:#ccc}
