*{padding:0;margin:0;box-sizing:border-box;scroll-behavior:smooth}body{overflow-x:hidden;font-family:"Poppins",sans-serif;color:#2a2a2a;font-size:calc(12px + (20 - 12) * ((100vw - 300px) / (1600 - 300)))}body a{color:#2a2a2a;transition:color .2s;text-decoration:none}body a:hover{color:#ccc}body h1{font-family:"Space Grotesk",sans-serif;font-size:calc(40px + (100 - 40) * ((100vw - 300px) / (1600 - 300)))}body h2{font-size:calc(18px + (30 - 18) * ((100vw - 300px) / (1600 - 300)))}.noscroll{overflow-y:hidden}::selection{background:#ccc}::-moz-selection{background:#ccc}::-webkit-scrollbar{width:5px}::-webkit-scrollbar-track{background:rgba(0,0,0,.5)}::-webkit-scrollbar-thumb{border-radius:0px;background:#2a2a2a}
